@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.main_header{
   background-image: url(/static/media/milad-fakurian-UiiHVEyxtyA-unsplash.a521a9c6.jpg);
   background-size:cover;
   background-color:rgb(99, 90, 99);
   height:80vh;
}
.img{
   width: 20rem;
   height:20rem;
   border-radius:50%;
   position: relative;
   bottom: 0;
   right:29rem;
   -webkit-filter:grayscale(50%);
           filter:grayscale(50%);
}
.img:hover{
   -webkit-transform:scale(1.3);
           transform:scale(1.3);
   transition:ease;
}
.coding-profile{
   width:30%;
   display:flex;
   margin:auto;
   justify-content: space-between;
   position: absolute;
   top:7em;
   right:14em;
}
.coding-profile > div{
   width: 7em;
   height: 3em;
   background:black;
   border-radius: .5em;
   text-align:center;
   color:white;
   padding-top:.7rem;
}
a{
   text-decoration:none;
   color:white;
}
.coding-profile > div:hover{
   background: gray;
}
.name{
   position: absolute;
   bottom:14em;
   right:17rem;
}

@media(max-width:650px){
   .main_header{
     font-size: 50%;
     height: 77vh; 
      
   }
   .name{
      position: relative;
      bottom:0rem;
      left:3rem;
   }
   .img{
      position: relative;
      right:6rem;
      height: 45%;
      width: 50%;
   }
}
*{
    margin: 0;
    padding: 0;
}
.about-container{
    background-image: url(/static/media/milad-fakurian-UiiHVEyxtyA-unsplash.a521a9c6.jpg);
    background-size:  cover;
    overflow: hidden;
}
#education{
    background-color:aliceblue;
    height:-webkit-max-content;
    height:max-content;
}

#project-title{
    background: black;
    height: 2em;
    color:white;
    margin-top:3.5em;
}
#projects{
    height:50vh; 
}
.ed-label{
    height: 2em;
    margin: 0.2em;
    font-size: 2rem;
}
#project-container{
    display: flex;
}
#s1{
    display:flex;
    flex-direction: column;
    width:5.5em;
