    border-radius: 1em;
}

.ed-content{
    height: 8em;
    width: 16em;
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
    background: rgba(255,255,255,0.7);
    border-radius: 1em;
  
}
.ed-container{
    width: 65%;
    height: 80%;
    border-radius: 1em;
    border: 1px solid black;
    margin: 1em;
   
    display:flex;
    flex-direction: row;
}
#records{
    padding-left: 10em;
}
  

#info-2{
    height: 30vh;
    width: 50%;
    float: right;
    margin:1em 2em 0 0;
    text-align: center;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;   
}
#info-1{
    border-radius: 50%;
    background: rgb(7, 7, 7);
    width: 6em;
    height: 6em;
    position:relative;
    left:4em;
    top:6em;
}
#info-1 a span{
    font-size:3em;
    position: relative;
    top:.6em;

}
.cv-button{
    background: black;
    height: 2.6em;
    width: 9em;
    position: absolute;
    bottom: 9em;
    left: 15em;
    border-radius: 4px;
    display: inline-block;
    text-decoration: none;
    color: aliceblue;
    text-align: center; 
    justify-content: center;
}

#section-1{
    width: 50%;
    height: 75vh;
    float:left;
}
#qualifications{
    float: right;
    height: 29em;
    width: 50%;
    border-left: 2px solid black; 
}
#qualifications > #title-name{
    text-align: center;
    background: black;
    color: white;
    margin-top: 1em;
    height: 7.5vh;
}
#about-1{
    height: 75vh;
   
    /* */
}
.project-card{
    width: 65%;
    height: 10em;
    display: flex;
    flex-direction: row;
    background-color:rgb(210, 216, 221);
    margin: 1em;
    margin-left: 5em;
    border: 1px solid black;
    border-radius: 1em;
    overflow: hidden;
}

.project-card >img{
    opacity: 80%;
    
}
.project-card > div{
    margin: auto;
    width: 70%;
}

.project-container{
    display: flex;
    width: 50%;
    flex-direction: column;
    margin-top: 1em;
    
}

.project-card:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition-delay: .1s;
    transition-timing-function: ease;
}
:root {
    --animate-delay: 3s;
  }
.skill-container{
    width: 50%;
    padding-left: 13em; 
}
.skills{
    width: 15em;
    height: 4em;
    background:rgb(210, 216, 255);
    border-radius: .5em;
    margin-top: 2em;
    border: 1px solid black;
}
.skills > div{
    width: 10%;
    border-bottom-right-radius: .5em;
    background:white;
}
.skills:nth-child(2n+1){
    position: relative;
    left: 4em;
}
.skills:nth-child(2n){
    position: relative;
    right: 4em;
}
 .container{
        padding:0;
        /* background-color */
        overflow: hidden;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        margin-top: 2em;
        margin-bottom: 1em;
        border-radius:1em;
         
}
.talk-div{
    background: rgb(234, 244, 245);
    width: 38%;
    height: 8em;
    margin: .4em;
    border-radius: 1em;
    border: 1px solid black;
    align-items: center;
    
}
.talk-div:hover{
    -webkit-transform: translateX(1);
            transform: translateX(1);
}
.c1{
    width: 50%;
    background:rgb(234, 244, 245);
    margin-right: 2em;
    display: flex;
    align-items: center;
    flex-direction: column;
}
#c1{
    margin-left:4em;
} 
#c2{
    margin-left:-5em;
}
.input{
    height: 8em;
    display: flex;
    flex-direction: column;
}
.title{
    font-size: 2rem;
}
form{
    height:120%;
}

input{
    border-radius: 0.6rem;
    padding: 0.4rem 12px;
    height: 9vh;
    width: 130%;
    display: block;
    border: 1px solid black;
}
.project{
    height: 20vh;
    
}
label{
    background: #fff;
    width: -webkit-min-content;
    width: min-content;
    padding: 0;
    position: relative;
    top: .8em;
    left: .9em;
}
button{
    padding: 0 1em;
    border-radius: .5rem;
    background-color: black;
    color: white;
   float: left;
    margin-top: 1em;
}
button >span{
    color: white;
    position: relative;
    top:.3em;
   
}


